.loginComponent {
    min-height: 100vh;
    position: relative;

    .logoImg {
        position: absolute;
        top: 104px;
        left: 112px;
    }

    .leftBottomImg {
        position: absolute;
        bottom: 0;
        left: 0;
        z-index: -999;
    }

    .rightBottomImg {
        position: absolute;
        right: 0;
        bottom: 0;
        z-index: -999;
    }

    .registerForm {
        width: 328px;
        margin: auto;
        padding-top: 96px;
    }

    .innerDiv {
        height: 100%;
        width: 100%;
    }

    .mainContainer {
        max-width: 1500px;
        margin: auto;
        position: relative;
        min-height: 100vh;
    }

    .createHeading {
        margin-bottom: 25px;
    }

    .brandLogoContainer {
        text-align: center;
        margin-bottom: 51px;
    }

    .dizcopyrights {
        position: absolute;
        bottom: 18px;
        left: 0;
        right: 0;
        text-align: center;
    }

    @media only screen and (max-width: 1000px) {
        .logoImg {
            position: absolute;
            top: 3%;
            left: 8%;
        }

        .mobileLogo {
            height: 41px;

            img {
                width: 72px;
                height: 37.34px;
            }
        }

        .innerDiv {
            margin-top: 48px;
        }

        .leftBottomImg {
            display: none;
        }

        .rightBottomImg {
            img {
                width: 230px;
            }
        }
    }
}